<template>
  <div class="p-4">
    <h3 class="my-4"></h3>
    <div class="card">
      <div class="card-body">
        <button class="btn btn-orange" @click="openModal('new')">
          <i class="fas fa-plus-circle"></i>
          
        </button>
        <div class="table-responsive mt-3">
          <table class="table text-center">
            <thead class="thead">
              <tr class="text-nowrap">
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in products" :key="item.id">
                <td>
                  <div
                    class="productImg"
                    :style="`backgroundImage:url('${item.imageUrl[0]}')`">
                  </div>
                </td>
                <td>{{ item.title }}</td>
                <td>{{ item.category }}</td>
                <td>{{ item.origin_price }}</td>
                <td>{{ item.price }}</td>
                <td>
                  <div class="custom-control custom-switch">
                    <input type="checkbox" class="custom-control-input"
                    :checked="item.enabled" disabled>
                    <label class="custom-control-label"></label>
                  </div>
                </td>
                <td class="text-right">
                  <button class="btn btn-outline-orange mx-1" @click="openModal('edit', item)">
                    <i class="far fa-edit"></i>
                  </button>
                  <button class="btn btn-outline-danger mx-1" @click="openModal('delete', item)">
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <Pagination :pages="pagination" @go-page="getProducts"/>
      </div>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="product"
      tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-xl">
        <div class="modal-content">
          <div class="modal-header">
            <div v-if="isNew">
              <h5 class="modal-title"></h5>
            </div>
            <div v-else>
              <h5 class="modal-title"> {{ temProduct.title }} </h5>
            </div>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group"
                  v-for="i in 5"
                  :key="i+'img'"
                >
                  <label :for="`img${i}`"></label>
                  <input type="text" placeholder="" :id="`img${i}`"
                  class="form-control" v-model="temProduct.imageUrl[i-1]">
                </div>
                <div class="form-group">
                  <label for="customFile"> 
                    <span v-if="status.fileUploading">
                      <i class="fas fa-spinner fa-spin"/>
                    </span>
                  </label>
                  <input
                  type="file"
                  id="customFile"
                  ref="file"
                  class="form-control"
                  @change="uploadFile"
                  >
                </div>
                <img
                    class="img-fluid"
                    :src="temProduct.imageUrl[0]"
                  >
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input id="title" type="text" placeholder=""
                  required="required" class="form-control" v-model="temProduct.title">
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input id="category" type="text" placeholder="" required="required"
                     class="form-control" v-model="temProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="unit"></label>
                    <input id="unit" type="text" placeholder=""
                    class="form-control" v-model="temProduct.unit">
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input id="origin_price" type="number" placeholder="" required="required"
                     class="form-control" v-model="temProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input id="price" type="number" placeholder=""
                    class="form-control" v-model="temProduct.price">
                  </div>
                </div>
                <hr/>
                <div class="form-group">
                  <label for="description"></label>
                  <textarea id="description" type="text" placeholder=""
                   required="required" class="form-control" v-model="temProduct.description">
                   </textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <VueEditor v-model="temProduct.content"/>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input id="is_enabled" type="checkbox"
                    class="form-check-input" v-model="temProduct.enabled">
                    <label for="is_enabled" class="form-check-label"></label>
                    </div>
                  </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-orange" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>

    <div
      id="delProduct"
      class="modal fade"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5
              id="exampleModalLabel"
              class="modal-title"
            >
              <span></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ temProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-danger"
              @click="delProduct"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* global $ */
import Pagination from '@/components/Pagination.vue';
import { VueEditor } from 'vue2-editor';

export default {
  components: {
    Pagination,
    VueEditor,
  },
  data() {
    return {
      products: [],
      pagination: {},
      temProduct: {
        imageUrl: [],
      },
      isNew: false,
      status: {
        fileUploading: false,
      },
    };
  },
  created() {
    this.getProducts();
  },
  methods: {
    // 
    getProducts(page = 1) {
      const loader = this.$loading.show();
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/products?page=${page}`;
      this.$http.get(api).then((res) => {
        this.products = res.data.data;
        this.pagination = res.data.meta.pagination;
        loader.hide();
      }).catch((error) => {
        console.log(error);
        loader.hide();
      });
    },
    getDetails(id) {
      const loader = this.$loading.show();
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      this.$http.get(api).then((res) => {
        this.temProduct = res.data.data;
        $('#product').modal('show');
        loader.hide();
      }).catch((error) => {
        console.log(error);
        loader.hide();
      });
    },
    openModal(isNew, item) {
      switch (isNew) {
        case 'new':
          this.temProduct = {
            imageUrl: [],
          };
          this.isNew = true;
          $('#product').modal('show');
          break;
        case 'edit':
          this.isNew = false;
          this.getDetails(item.id);
          break;
        case 'delete':
          this.temProduct = { ...item };
          $('#delProduct').modal('show');
          break;
        default:
          break;
      }
    },
    updateProduct() {
      const loader = this.$loading.show();
      // 
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product`;
      let httpMethod = 'post';
      if (!this.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.temProduct.id}`;
        httpMethod = 'patch';
      }
      this.$http[httpMethod](api, this.temProduct).then(() => {
        $('#product').modal('hide');
        this.getProducts();
        loader.hide();
      }).catch((error) => {
        console.log(error);
        loader.hide();
      });
    },
    delProduct() {
      const loader = this.$loading.show();
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.temProduct.id}`;
      this.$http.delete(api).then(() => {
        loader.hide();
        $('#delProduct').modal('hide');
        this.getProducts();
      }).catch((error) => {
        loader.hide();
        console.log(error);
      });
    },
    uploadFile() {
      // DOM
      const uploadFile = this.$refs.file.files[0];
      const formData = new FormData();
      // file
      formData.append('file', uploadFile);
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/storage`;
      this.status.fileUploading = true;
      this.$http.post(api, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((res) => {
        this.status.fileUploading = false;
        console.log(res);
        if (res.status === 200) {
          this.temProduct.imageUrl.push(res.data.data.path);
        }
      }).catch((error) => {
        this.status.fileUploading = false;
        console.log(error);
      });
    },
  },
};
</script>
<style lang="scss" scoped>
  .productImg {
    width: 100%;
    min-width: 100px;
    height: 100px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }
</style>
